import './App.css';
import './MainRow.css';
import MainRowImg from './assets/img/main-row-img.svg';


function MainRow() {
    return (
        <div className="MainRow">
            <div className="container">
                <div className="main-row-top__text">
                    <span className="text-row">P o w e r⠀and⠀F l e x i b i l i t y</span>
                    <span className="main-row-top_span">
                    Manage a fleet of digital experiences from <br/>
                    a single dashboard. Customize your no-<br/>
                    code website factory with the flexibility of<br/>
                    a modern open source tech stack.
                    </span>
                </div>
                {/* block */}
                <div className="main-row-block">
                    <div className="main-row-container">
                        <div className="main-row__img">
                            <img src={MainRowImg} alt={"MainRowImg"}/>
                        </div>
                        <div className="main-row-blocks">
                            <div className="row-block">
                                <span className="row-block-text">
                                    Drag/drop in-context live editing that empowers 
                                    editors to make changes in real time
                                </span>
                            </div>
                            <div className="row-block">
                                <span className="row-block-text">
                                    Visual design tools that allow for dynamic styling 
                                    while maintaining brand guidelines
                                </span>
                            </div>
                            <div className="row-block">
                                <span className="row-block-text">
                                    Integrate your tech with native modules and flexible content APIs
                                </span>
                            </div>
                            <div className="row-block">
                                <span className="row-block-text">
                                    One click to add a new website propelling your speed to market by 10x
                                </span>
                            </div>
                            <div className="row-block">
                                <span className="row-block-text">
                                    One dashboard to oversee it all
                                </span>
                            </div>          
                               
                            <a href="/" className="row-btn">Learn more about Apostrophe Assembly</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}


export default MainRow;


