import './App.css';
import './Main.css';
import MainBanner from './assets/img/banner.svg'

function Main() {
    return (
        <div className="Main">
            <div className="container">
                <div className="banner">
                    <div className="banner_container">
                        <div className="banner-text-block">
                            <span className="banner-text">
                                The open source website builder for your business.
                            </span>
                            <span className="banner-text-description">
                                Apostrophe Assembly is a powerful and flexible website
                                builder platform for digital agencies, SaaS companies, 
                                higher education, enterprise, and more.
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Main;